
*{box-sizing:border-box
}
body,h1,h2,h3,h4,h5,h6,p,ul,li,a{margin:0;padding:0
}
main,nav,header,footer,article,aside{display:block
}
a{text-decoration:none;color:inherit
}
a:focus{outline-width:0
}
html{font-size:14px;font-family:sans-serif;background-color:#f8f9fa;color:#212529
}
body{font-size:1rem;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;line-height:1.5
}
img,svg{display:block
}
input{font-size:1rem;font-family:inherit
}
input:focus{outline-width:0
}
.search{width:auto;margin:25px 0;overflow:hidden;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;box-shadow:0 2px 3px rgba(0,0,0,0.26);border:1px solid rgba(0,0,0,0.08);border-radius:2px
}
.search__input{display:block;border-width:0;-ms-flex:1;flex:1;padding:15px 20px;font-size:1.2rem
}
.search__mic-icon{height:24px;width:24px;margin:0 20px;cursor:pointer
}
.btn{background-color:#f1f3f5;color:#495057;font-size:1rem;padding:8px 16px;display:inline-block;font-weight:500;border-radius:2px;text-transform:capitalize;position:relative;overflow:hidden
}
.btn::after{content:'';position:absolute;height:calc(100% - 2px);width:calc(100% - 2px);left:0;top:0;border:solid 1px rgba(0,0,0,0.1);border-radius:2px;opacity:0
}
.btn:hover{color:#212529
}
.btn:hover::after{opacity:1
}
.app{min-height:100vh;width:100%;display:-ms-flexbox;display:flex;-ms-flex-flow:column nowrap;flex-flow:column nowrap;-ms-flex-pack:justify;justify-content:space-between;position:relative;background-color:#f8f9fa
}
.main{display:-ms-flexbox;display:flex;-ms-flex-flow:column nowrap;flex-flow:column nowrap;width:50%;margin:auto
}
.main__hero{width:50%;margin:auto
}
.search-buttons{display:-ms-flexbox;display:flex;width:100%;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.header{width:100%;position:relative;padding:5px 20px
}
.nav{width:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:end;justify-content:flex-end;-ms-flex-align:center;align-items:center
}
.nav__item{padding:10px;text-decoration:none;color:inherit;position:relative;color:#495057;cursor:pointer
}
.nav__item>svg{fill:#495057;height:24px;width:24px
}
.nav__item--profile>img{height:35px;width:35px;border-radius:50%
}
.nav__item--bell>svg{padding:5px;border-radius:50%;background-color:#495057
}
.nav__item--bell>svg>path{fill:#f8f9fa
}
.footer{background-color:#f1f3f5;width:100%;border-top:solid 1px rgba(0,0,0,0.1);display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;padding:0 15px
}
.footer-nav__item{display:inline-block;padding:10px 15px
}
.footer-nav__item:hover{text-decoration:underline
}



